.buttons_2_pagePart{display:flex;flex-direction:column;gap:.8rem;gap:10px;width:100%}.buttons_2_pagePart__button{display:flex;flex-direction:row;align-items:center;gap:.8rem;padding:1rem;border-radius:4px;background-color:rgba(0,0,0,0);color:var(--second-text-color, #fff);font-size:1rem;white-space:wrap;cursor:pointer;transition:background-color .1s ease,color .1s ease}.buttons_2_pagePart__button.active{background-color:var(--charcoal-green, #1c2624);color:rgb(var(--first-text-color, #fff))}.buttons_2_pagePart__button__icon{min-width:1rem;height:1rem}